.country {
  width: 75vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  padding: 20px;
  border: 1px solid #cecece;
  border-right: none;
  border-left: none;
  &__name {
    font-weight: bold;
  }
  &__details {
    display: inherit;
    &-data {
      margin-right: 20px;
      display: flex;
      align-items: center;
      align-items: center;
      img {
        width: 30px;
        height: 30px;
        margin-right: 5px;
      }
    }
  }
}

@media screen and (max-width: 412px) {
  .country {
    width: 100vw;
    padding: 20px 2px;
    flex-direction: column;

    &__name {
      font-weight: bold;
      font-size: 26px;
      margin-bottom: 10px;
      align-self: flex-start;
    }
    &__details {
      display: inherit;
      font-size: 18px;
      &-data {
        margin-right: 8px;
        display: flex;
        align-items: center;
        align-items: center;
        img {
          width: 20px;
          height: 20px;
          margin-right: 5px;
        }
      }
    }
  }
}
